import React, { PureComponent } from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  withStyles,
  Link,
  IconButton,
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import MainDrawer from './MainDrawer';

const styles = {
  root: {
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
};
const menuList = [
  {
    primary: 'IDC1',
    secondary: 'ใบขนสินค้าขาเข้า 1',
  },
  {
    primary: 'IDC2 (Invoice)',
    secondary: 'ใบขนสินค้าขาเข้า 2',
  },
  {
    primary: 'IDD1 (Detail)',
    secondary: 'ใบขนสินค้าขาเข้า 3',
  },
  {
    primary: 'IDD2 (Duty)',
    secondary: 'ใบขนสินค้าขาเข้า 4',
  },
  {
    primary: 'IDD3 (Permit)',
    secondary: 'ใบขนสินค้าขาเข้า 5',
  },
  {
    primary: 'IDD4 (Deposit)',
    secondary: 'ใบขนสินค้าขาเข้า 6',
  },
];
const links = ['/idc1', '/idc2', '/idd1', '/idd2', '/idd3', '/idd4'];

class TopNav extends PureComponent {
  state = {
    menuOpen: false,
  };

  toggleOpen = () => {
    this.setState({
      menuOpen: true,
    });
  };

  toggleClose = () => {
    this.setState({
      menuOpen: false,
    });
  };

  render() {
    const { classes } = this.props;
    return (
      <div className={classes.root}>
        <AppBar position="static">
          <Toolbar>
            <IconButton
              className={classes.menuButton}
              color="inherit"
              aria-label="Menu"
              onClick={this.toggleOpen}
            >
              <MenuIcon />
            </IconButton>
            <MainDrawer
              menuList={menuList}
              links={links}
              onClose={this.toggleClose}
              onOpen={this.toggleOpen}
              open={this.state.menuOpen}
            />
            <Typography>
              <Link href="/">HEAVY STONE</Link>
            </Typography>
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}

export default withStyles(styles)(TopNav);
